import React, { Component } from 'react'
import { toArray } from 'lodash';
import './Modal.scss';
import { connect } from 'react-redux';
import { getCustomers, getCustomersPhones } from 'reduxStore';
import { Button, Modal, ModalHeader, ModalBody } from 'reactstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { deleteCustomer } from 'services/SacMauMoiService';

// const options = {
//   clearSearch: true
// };

class ModalChooseCustomer extends Component {
  priceFormatter = (cell, row) => {
    return 'dong ' + cell;
  }

  onChoose = (soDienThoai) => {
    const { close, getCustomerByPhone } = this.props;
    return (
      <Button size='sm' color='primary'
        onClick={() => { getCustomerByPhone({ soDienThoai }); close(); }}
      >Chọn</Button>
    )
  }

  options = {
    sizePerPage: 25,
    onDeleteRow: (customers) => {
      // alert('Sẽ xoá các khách hàng: ' + customers.map(customer => customer) + '\nNếu anh thấy ok em sẽ update...')
      customers.map(customer => {
        deleteCustomer({ customer });
        return null;
      })
      this.props.getCustomers();
      this.props.getCustomersPhones();
    }
  }

  render() {
    const { isOpen, customers } = this.props;
    const { close } = this.props;
    const closeBtn = <button className="close" onClick={close}>&times;</button>;
    const khachhangs = toArray(customers);
    return (
      <div id="orderDetailModal">
        <Modal isOpen={isOpen} toggle={close} size="lg" fade={false}>
          <ModalHeader toggle={close} close={closeBtn}>Khách Hàng</ModalHeader>
          <ModalBody>
            <BootstrapTable data={khachhangs} striped={true} hover={true}
              search pagination options={this.options}
              selectRow={{ mode: 'checkbox' }} deleteRow={true}
            // remote={true}
            >
              <TableHeaderColumn dataField='soDienThoai' dataFormat={this.onChoose}>Action</TableHeaderColumn>
              <TableHeaderColumn dataField='congTy' isKey={true}>Công ty</TableHeaderColumn>
              <TableHeaderColumn dataField='soDienThoai' >SĐT</TableHeaderColumn>
              <TableHeaderColumn dataField='diaChi'>Địa chỉ</TableHeaderColumn>
              <TableHeaderColumn dataField='nguoiDatHang'>Liên hệ</TableHeaderColumn>
            </BootstrapTable>
          </ModalBody>
        </Modal>
      </div>

    )
  }
}

ModalChooseCustomer.propTypes = {

}

const mapStateToProps = state => {
  return {
    customers: state.sacMauMoi.customers
  }
}

export default connect(mapStateToProps, {
  getCustomers, getCustomersPhones
})(ModalChooseCustomer)
