const createHeaderTable = () => {
	const data = ['STT', 'Sản Phẩm', 'Chi tiết', 'ĐVT', 'Số lượng', 'Đơn giá', 'Thành tiền'];
	return data.map(text => {
		return [{ text, style: 'tableHeader', alignment: 'center' }]
	})
}
const createHeaderTable_External = () => {
	const data = ['STT', 'Sản Phẩm', 'Chi tiết', 'ĐVT', 'Số lượng'];
	return data.map(text => {
		return [{ text, style: 'tableHeader', alignment: 'center' }]
	})
}

const createBodyTable = (sanPhams) => {
	return sanPhams.map((sanpham, i) => {
		const { tenSanPham, moTa, donViTinh, soLuong, donGia, thanhTien, } = sanpham;
		return [
			{ text: i + 1, style: 'textTable', alignment: 'center' },
			{ text: (tenSanPham), style: 'textTable', alignment: 'left' },
			{ text: (moTa), style: 'textTable', alignment: 'left' },
			{ text: (donViTinh), style: 'textTable', alignment: 'center' },
			{ text: formatMoney(soLuong), style: 'textTable', alignment: 'right' },
			{ text: formatMoney(donGia), style: 'textTable', alignment: 'right' },
			{ text: formatMoney(thanhTien), style: 'textTable', alignment: 'right' },
		]
	})
}
const createBodyTable_External = (sanPhams) => {
	return sanPhams.map((sanpham, i) => {
		const { tenSanPham, moTa, donViTinh, soLuong, } = sanpham;
		return [
			{ text: i + 1, style: 'textTable', alignment: 'center' },
			{ text: (tenSanPham), style: 'textTable', alignment: 'left' },
			{ text: (moTa), style: 'textTable', alignment: 'left' },
			{ text: (donViTinh), style: 'textTable', alignment: 'center' },
			{ text: formatMoney(soLuong), style: 'textTable', alignment: 'right' },
		]
	})
}

const createFooterTable = (donhang) => {
	const { tongTien, tamUng, conLai, ghiChuKhachHang } = donhang;
	const tableFooter = [
		// [{ rowSpan: 3, colSpan: 4, text: '*Ghi chú: Đơn giá chưa bao gồm thuế VAT 10%', style: 'thueVAT' }, '', '', '',
		[
			{ rowSpan: 3, colSpan: 5, text: `*Ghi chú: ${ghiChuKhachHang}`, style: 'ghiChuKhachHang' }, '', '', '', '',
			{ text: 'Tổng tiền', style: 'tableHeader', alignment: 'center' },
			{ text: formatMoney(tongTien), style: 'textTable', alignment: 'right' }
		],
		['', '', '', '', '',
			{ text: 'Ứng trước', style: 'tableHeader', alignment: 'center' },
			{ text: (isEmpty(tamUng) ? '' : '- ') + formatMoney(tamUng), style: 'textTable', alignment: 'right' },
		],
		['', '', '', '', '',
			{ text: 'Còn lại', style: 'tableHeader', alignment: 'center' },
			{ text: formatMoney(conLai), style: 'textTable', alignment: 'right', bold: true },
		],
	];

	return tableFooter;
}
const createFooterTable_External = (donhang) => {
	const { ghiChuKhachHang } = donhang;
	const tableFooter = [
		[{ colSpan: 5, text: `*Ghi chú: ${ghiChuKhachHang}`, style: 'ghiChuKhachHang' }, '', '', '', '']
	];

	return tableFooter;
}

export const makePDF = ({ donhang, khachhang, type }) => {
	const { vfs } = vfsFonts.pdfMake;
	pdfMake.vfs = vfs;

	const docDefinition = {
		pageSize: 'A5',
		pageOrientation: 'landscape',
		pageMargins: [15, 15, 15, 15],
		content: [
			createHeader(),
			{ canvas: [{ type: 'line', x1: 0, y1: 0, x2: 560, y2: 0, lineWidth: 0.5 }] },
			createThongTinChiTiet({ donhang, khachhang }),
			{
				table: {
					headerRows: 1,
					dontBreakRows: true,
					widths: [23, '*', 165, 40, 45, 50, 70],
					body: [
						createHeaderTable(),
						...createBodyTable(donhang.sanPhams),
						...createFooterTable(donhang),
					]
				}
			},
			// 
			{
				columns: [
					{ text: 'Người nhận hàng', style: 'nguoiNhanHangStyle', alignment: 'right', width: 120},
					{ text: '*Lưu ý: Giá trên chưa bao gồm thuế VAT 10% (Nếu xuất HĐ VAT vui lòng thanh toán thêm 10%)'
					, style: 'thueVAT', alignment: 'right', width: '*'},
				]
			},
			{ text: '', pageBreak: 'before' }, // another page
			createHeader_External(),
			{ canvas: [{ type: 'line', x1: 0, y1: 0, x2: 560, y2: 0, lineWidth: 0.5 }] },
			createThongTinChiTiet({ donhang, khachhang }),
			{
				table: {
					headerRows: 1,
					dontBreakRows: true,
					widths: [23, 140, '*', 40, 45],
					body: [
						createHeaderTable_External(),
						...createBodyTable_External(donhang.sanPhams),
						...createFooterTable_External(donhang),
					]
				}
			},
			// 
			{
				columns: [
					{ text: 'Thiết kế', style: 'nguoiThietKe', alignment: 'center', width: 130 },
					{ text: 'Ngày...... tháng...... năm 20.....', style: 'ngayThangNam', alignment: 'left', }
				]
			}
		],
		styles: styles,
	};

	const win = window.open('', '_blank');
	pdfMake.createPdf(docDefinition).open({}, win);
}