import React from 'react'
import { Badge } from 'reactstrap'
import PropTypes from 'prop-types'
// import { DANG_LAM, HOAN_THANH } from '../../../constants/Constants';
import { DA_GIAO, DA_HUY, HOAN_THANH } from '../../../constants/Constants';

const propTypes = {
  trangThai: PropTypes.string.isRequired,
}

const TrangThaiComponent = ({ trangThai }) => {
  let color = '';
  switch (trangThai) {
    case HOAN_THANH: { color = 'success'; break; }
    case DA_HUY: { color = 'danger'; break; }
    case DA_GIAO: { color = 'warning'; break; }
    default: { color = 'light'; break; }
  }
  return <Badge color={color}>{trangThai}</Badge>
}

TrangThaiComponent.propTypes = propTypes;

export default TrangThaiComponent
