import React from 'react'
import { isEmpty } from 'lodash'
import { Modal as ModalComponent, ModalHeader, ModalBody, ModalFooter, Row, Col } from 'reactstrap';
import { toast } from 'react-toastify';
import './Modal.scss';

// import { SanPhamIn } from 'views/Pages/ThemDonHang/components';
import { DA_HUY, DANG_LAM, HOAN_THANH, MESSAGE_UPDATE_NGAYGIAO_SUCCESS, MESSAGE_UPDATE_STATUS_SUCCESS } from 'constants/Constants';
import { addOrder, formatMoney, getDate } from 'services/SacMauMoiService';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import TrangThaiComponent from '../TrangThaiComponent/TrangThaiComponent';
import { DA_GIAO } from '../../../constants/Constants';

class Modal extends React.Component {

  static defaultProps = {
    deliveryMessage: '',
  }
  state = {
    ngayGiao: '',
    isUpdateNgayGiao: false,
  }

  changeTrangThai = ({ trangThai, donhang, getDonHangs }) => {
    const { typeSeeHoaDon, year, toMonth, fromMonth } = this.props;
    if (trangThai === donhang.trangThai) { return; }

    let updateNgayGiao = false;
    if (trangThai === DA_HUY) {
      let check = window.confirm('Xác nhận huỷ đơn hàng?');
      if (check === false) return;
    }

    if (trangThai === DA_GIAO && isEmpty(donhang.ngayGiao)) {
      updateNgayGiao = true;
      donhang.ngayGiao = getDate({ date: new Date() });
    }

    donhang.trangThai = trangThai;
    addOrder({ donhang }).then(() => {
      if (updateNgayGiao) {
        toast.success(MESSAGE_UPDATE_NGAYGIAO_SUCCESS);
      }
      toast.success(MESSAGE_UPDATE_STATUS_SUCCESS);
    });
    getDonHangs({ type: typeSeeHoaDon, year, toMonth, fromMonth });
  }

  formatMoney = (money) => {
    return formatMoney(money);
  }

  onChangeNgayGiao = (e) => {
    const { donhang } = this.props;
    if (donhang.ngayGiao !== e.target.value) {
      return this.setState({ [e.target.name]: e.target.value, isUpdateNgayGiao: true })
    }
    return this.setState({ [e.target.name]: e.target.value, isUpdateNgayGiao: false })
  }

  ngayGiaoRender = () => {
    const { donhang } = this.props;
    if (donhang.trangThai === DA_HUY) {
      return <span className="mr-2">{donhang.ngayGiao}</span>;
    }
    return <input type="date" name="ngayGiao" value={this.state.ngayGiao} onChange={this.onChangeNgayGiao} />
  }

  componentWillReceiveProps(props) {
    if (props.donhang && props.donhang.ngayGiao) {
      this.setState({ ngayGiao: props.donhang.ngayGiao })
    } else {
      this.setState({ ngayGiao: "" })
    }

  }

  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }

  updateNgayGiao = () => {
    const { donhang } = this.props;
    const newDonhang = { ...donhang };
    newDonhang.ngayGiao = this.state.ngayGiao;
    addOrder({ donhang: newDonhang }).then(() => {
      this.props.getDonHangs();
      this.setState({ isUpdateNgayGiao: false });
      toast.success(MESSAGE_UPDATE_NGAYGIAO_SUCCESS);
    });
  }

  render() {
    const { donhang, isOpen } = this.props;
    const { close } = this.props;
    const closeBtn = <button className="close" onClick={close}>&times;</button>;
    if (isEmpty(donhang)) {
      return null;
    }
    // const newDate = new Date(donhang.createAt)
    // console.log(newDate.getFullYear(), newDate.getMonth()+1)

    return (
      <div id="orderDetailModal">
        <ModalComponent
          className="modal-dialog-centered"
          size="lg"
          // overlayClassName="Overlay"
          isOpen={isOpen}
          toggle={close}
          contentLabel="Modal #2 Global Style Override Example"
          onRequestClose={close}
        >
          <ModalHeader toggle={close} close={closeBtn}>Chi tiết đơn hàng</ModalHeader>
          <ModalBody>
            {isEmpty(donhang) ? <div>Loading</div> :
              <Row>
                <Col>
                  <div>Khách hàng: {donhang.khachhang.congTy}</div>
                  <div>Trạng thái: <TrangThaiComponent trangThai={donhang.trangThai} /></div>
                  <div>Số điện thoại: {donhang.khachhang.soDienThoai}</div>
                  <div>Ngày nhận: {donhang.ngayNhan}</div>
                  <div>
                    Ngày giao: {this.ngayGiaoRender({ ngayGiao: donhang.ngayGiao })}
                    <button onClick={this.updateNgayGiao} className="btn btn-sm btn-success" disabled={!this.state.isUpdateNgayGiao}>update</button>
                  </div>
                  <div>Tổng tiền: {formatMoney(donhang.tongTien)}</div>
                  <div>Tạm ứng: {isEmpty(donhang.tamUng) ? '' : '-'} {formatMoney(donhang.tamUng)}</div>
                  <div>Còn lại: <strong>{formatMoney(donhang.conLai)}</strong></div>
                </Col>
                <Col >
                  <a className="btn btn-sm btn-danger w-100" href={`tel:${donhang.khachhang.soDienThoai}`}>Gọi {donhang.khachhang.congTy}</a> <br />
                  <a className="btn btn-sm btn-success w-100 mt-1" href={`sms:${donhang.khachhang.soDienThoai}?body=${this.props.deliveryMessage}`}>Nhắn tin nhận hàng Android</a> <br />
                  <a className="btn btn-sm btn-success w-100 mt-1" href={`sms:${donhang.khachhang.soDienThoai}&body=${this.props.deliveryMessage}`}>Nhắn tin nhận hàng Iphone</a>  <br /><br />
                </Col>
                <br />
                <BootstrapTable data={donhang.sanPhams} hover={true} striped={true} condensed={true} bordered={false}>
                  <TableHeaderColumn width="150" dataField='tenSanPham' isKey={true}>Tên sản phẩm</TableHeaderColumn>
                  <TableHeaderColumn width="150" dataField='moTa'>Mô tả</TableHeaderColumn>
                  <TableHeaderColumn width="100" dataField='donViTinh' dataAlign='center'>Đơn vị tính</TableHeaderColumn>
                  <TableHeaderColumn width="100" dataField='soLuong' dataFormat={this.formatMoney} dataAlign='right'>Số lượng</TableHeaderColumn>
                  <TableHeaderColumn dataField='donGia' dataFormat={this.formatMoney} dataAlign='right'>Đơn giá</TableHeaderColumn>
                  <TableHeaderColumn dataField='thanhTien' dataFormat={this.formatMoney} dataAlign='right'>Thành tiền</TableHeaderColumn>
                </BootstrapTable>
              </Row>
            }

          </ModalBody>
          <ModalFooter>
            <button
              disabled={donhang.trangThai === DA_HUY}
              className="btn btn-secondary mr-2"
              onClick={() => { this.changeTrangThai({ trangThai: DANG_LAM, ...this.props }) }}>{DANG_LAM}</button>
            <button
              disabled={donhang.trangThai === DA_HUY}
              className="btn btn-success mr-2"
              onClick={() => { this.changeTrangThai({ trangThai: HOAN_THANH, ...this.props }) }}>{HOAN_THANH}</button>
            <button
              disabled={donhang.trangThai === DA_HUY}
              className="btn btn-warning mr-2"
              onClick={() => { this.changeTrangThai({ trangThai: DA_GIAO, ...this.props }) }}>{DA_GIAO}</button>
            <button
              disabled={donhang.trangThai === DA_HUY}
              className="btn btn-danger"
              onClick={() => { this.changeTrangThai({ trangThai: DA_HUY, ...this.props }) }}>Huỷ đơn hàng</button>
          </ModalFooter>
        </ModalComponent>
      </div>

    )
  }
}

Modal.propTypes = {

}

export default Modal
